import { onMounted, watch } from "vue";
import { ElMessage, ElMessageBox } from "element-plus";
import { Delete as icon_delete, Edit as icon_edit, Search as icon_search, RefreshRight as icon_refresh } from "@element-plus/icons-vue";
import commonFn from "@/hooks/index";
import indexFn from "@/hooks/pages/clinic";
export default {
  setup() {
    // 以下：頁面共用組件
    const {
      pageSize,
      currPage,
      searchMap,
      mode,
      dialogFormVisible,
      handleCurrentChange,
      handleSizeChange,
      hasPermission,
      userInfo
    } = commonFn(); // 以下：該頁組件切割

    const {
      store,
      list,
      rules,
      form,
      clearForm,
      formRef,
      listLoading
    } = indexFn();

    const Clear = () => {
      Object.assign(form, clearForm);
    }; // 以下：表單模式切換


    const Add = () => {
      if (mode.value !== "add") {
        mode.value = "add";
        Clear();
      }

      dialogFormVisible.value = true;
    };

    const Edit = (_id, _mode) => {
      if (mode.value !== _mode) mode.value = _mode;
      const editData = list.value.find(a => a.clinic_id === _id);
      Object.assign(form, editData);
      dialogFormVisible.value = true;
    };

    const handleSubmit = formRef => {
      formRef.validate(valid => {
        if (valid) {
          if (mode.value === "add") Insert();else if (mode.value === "update") Update();
        } else ElMessage({
          message: "請確認表單是否符合格式!",
          type: "error"
        });
      });
    };

    const Copy = text => {
      console.log(text);
      navigator.clipboard.writeText(text);
    }; // 以下：CRUD
    // mounted後獲取資料


    onMounted(() => {
      fetchData();
    });

    const fetchData = async () => {
      await store.dispatch("clinic/getClinics");
      Search();
    };

    const afterRequest = res => {
      if (res?.msg) {
        Search();
        ElMessage({
          message: res.msg,
          type: "success"
        });
        dialogFormVisible.value = false;
        if (mode.value === "add") Clear();
      }
    };

    const Insert = async () => {
      const res = await store.dispatch("clinic/insertClinic", {
        name: form.name,
        start_at: form.start_at.toISOString().split("T")[0],
        end_at: form.end_at.toISOString().split("T")[0],
        quota_per_month: form.quota_per_month
      });
      afterRequest(res);
    };

    const Update = async () => {
      const res = await store.dispatch("clinic/updateClinic", {
        clinic_id: form.clinic_id,
        name: form.name,
        start_at: form.start_at instanceof Date ? form.start_at.toISOString().split("T")[0] : form.start_at,
        end_at: form.end_at instanceof Date ? form.end_at.toISOString().split("T")[0] : form.end_at,
        quota_per_month: form.quota_per_month
      });
      afterRequest(res);
    };

    const UpdateToken = _id => {
      ElMessageBox.confirm("是否要更新該診所Token?", "注意", {
        confirmButtonText: "是",
        cancelButtonText: "否",
        type: "warning"
      }).then(async () => {
        const res = await store.dispatch("clinic/updateClinicToken", {
          clinic_id: _id
        });
        afterRequest(res);
      });
    };

    const Delete = _id => {
      ElMessageBox.confirm("是否要刪除該筆資料?", "注意", {
        confirmButtonText: "是",
        cancelButtonText: "否",
        type: "warning"
      }).then(async () => {
        const res = await store.dispatch("clinic/deleteClinic", {
          clinic_id: _id
        });
        afterRequest(res);
      });
    };

    const Search = async () => {
      if (String(searchMap.word) !== "") await store.dispatch("clinic/filter", { ...searchMap
      });else await store.dispatch("clinic/noFilter");
    }; // reactive要用 () => $var, ref用$var


    watch(() => searchMap.word, () => {
      if (String(searchMap.word) === "") Search();
    });
    return {
      // ICON
      icon_delete,
      icon_edit,
      icon_search,
      icon_refresh,
      // DATA
      list,
      // 控制狀態
      listLoading,
      dialogFormVisible,
      // 分頁
      pageSize,
      currPage,
      handleCurrentChange,
      handleSizeChange,
      // 表單
      searchMap,
      form,
      formRef,
      rules,
      // 提交模式
      mode,
      // 開啟表單
      Add,
      Edit,
      handleSubmit,
      // CRUD
      Insert,
      Update,
      UpdateToken,
      Delete,
      Copy,
      Search,
      hasPermission,
      userInfo
    };
  }

};